html {
  font-size: 62.5%;
  font-family: 'Arial', sans-serif; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

/* Don't kill focus outline for keyboard users: http://24ways.org/2009/dont-lose-your-focus */
a:hover, a:active {
  outline: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.reviews-box {
  min-width: 360px;
  max-width: 720px;
  padding: 2rem;
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

.title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%; }
  .title__img-box {
    border-radius: 50%;
    z-index: 10; }
  .title__img {
    width: 12rem;
    height: 12rem; }
  .title__description-box {
    text-align: left;
    padding-left: 1rem;
    width: 100%; }
  .title__name {
    font-size: 1.6rem;
    margin: .8rem 0;
    font-weight: 700;
    color: black; }
  .title__position {
    font-size: 1.2rem;
    color: #808080;
    margin: .8rem 0; }
  .title__summary {
    font-size: 1.4rem;
    line-height: 2rem;
    background-color: #fffbc8;
    border: 0.1rem solid #cccccc;
    padding: .8rem .8rem .8rem 7rem;
    border-radius: 0.5rem;
    margin: .8rem 0 1rem -7rem;
    z-index: 0; }

.services {
  margin-left: 12rem;
  margin-bottom: 1.3rem;
  padding-left: 1rem;
  padding-top: .3rem; }
  .services__title {
    text-align: end;
    padding: 0 3rem 1rem 0;
    border-bottom: 0.1rem solid #cccccc;
    font-size: 1.3rem; }
  .services .services-list {
    margin: 1.3rem 0;
    border-left: .1rem solid black; }
    .services .services-list__item-box {
      display: flex;
      justify-content: space-between;
      padding-right: 3rem;
      margin-bottom: 0.3rem;
      position: relative; }
      .services .services-list__item-box_total {
        border-top: 0.1rem solid #cccccc;
        padding: 1.3rem 3rem 1.3rem 0; }
    .services .services-list__count-view, .services .services-list__count-view_max {
      height: 100%;
      position: absolute;
      left: 0;
      z-index: 0;
      border-radius: 0 .4rem .4rem 0; }
    .services .services-list__count-view {
      background-color: #ace2f8; }
      .services .services-list__count-view_max {
        background-color: #b1e19b; }
    .services .services-list__item-title, .services .services-list__item-count {
      line-height: 2.3rem;
      font-size: 1.3rem;
      z-index: 10; }
    .services .services-list__item-count {
      font-weight: 700; }
    .services .services-list__total-title, .services .services-list__total-count {
      font-size: 1.6rem;
      font-weight: 700; }

.reviews {
  margin-bottom: 1rem; }
  .reviews__total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: .8rem; }
  .reviews__toggle-view, .reviews__indicators-view {
    display: flex;
    align-items: center; }
  .reviews__toggle-view {
    justify-content: flex-start;
    width: 70%; }
  .reviews__indicators-view {
    width: 30%;
    justify-content: flex-end; }
  .reviews__active, .reviews__not-active {
    border: none;
    outline: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    margin-right: 2rem;
    text-align: left; }
  .reviews__active {
    font-size: 1.6rem;
    font-weight: 700;
    color: black; }
  .reviews__not-active {
    font-size: 1.4rem;
    text-decoration: underline;
    color: #005c9f; }
  .reviews__indicators_likes, .reviews__indicators_comments {
    position: relative;
    font-size: 1.4rem;
    letter-spacing: -.1rem; }
    .reviews__indicators_likes::before, .reviews__indicators_comments::before {
      position: absolute;
      top: 0;
      left: -2rem;
      content: url("../assets/like.png"); }
  .reviews__indicators_likes::before {
    content: url("../assets/like.png"); }
  .reviews__indicators_comments {
    margin-left: 4rem; }
    .reviews__indicators_comments::before {
      content: url("../assets/comment.png"); }
  .reviews__item {
    padding: 1rem 0; }
  .reviews__name {
    font-size: 1.4rem;
    font-weight: 700;
    margin-bottom: 1.3rem; }
  .reviews__date {
    font-size: 1.1rem;
    color: #808080;
    font-weight: 400;
    margin-left: 0.5rem; }
  .reviews__text-box {
    padding: 1.3rem;
    background-color: #f2fbff;
    border: 1px solid #cccccc;
    position: relative; }
    .reviews__text-box:before, .reviews__text-box:after {
      position: absolute;
      bottom: 100%;
      content: ''; }
    .reviews__text-box:before {
      border: 0.5rem solid;
      border-color: transparent transparent #cccccc #cccccc;
      left: 1.9rem; }
    .reviews__text-box:after {
      border: 0.4rem solid;
      border-color: transparent transparent #f2fbff #f2fbff;
      left: 2rem; }
  .reviews__text {
    font-size: 1.4rem;
    line-height: 1.8rem;
    text-align: justify; }

.reviews__add {
  margin: 0 -2rem -2rem -2rem;
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  height: 100%;
  flex-grow: 1; }

.reviews__input {
  resize: none;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 2.6rem;
  font-size: 1.4rem;
  line-height: 1.8rem;
  text-align: justify; }

.reviews__error {
  font-size: 1rem;
  color: #808080;
  margin-bottom: -1rem; }

.reviews__button {
  font-size: 1.6rem;
  font-weight: 700;
  background-color: #fdd639;
  padding: 1rem 6rem;
  border-radius: 4rem;
  border: none;
  outline: none;
  cursor: pointer;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }
